.photo {
  width: 300px;
  height: 150px;
}

.img-wrap {
  position: relative;
}
.img-wrap .close {
  position: absolute;
  top: 2px;
  right: 0;
  z-index: 100;
  color: red;
  cursor: pointer;
}

.imgDiv {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 20px;
}

.deleteAllDiv {
  align-items: center;
  display: flex;
  cursor: pointer;
  width: 15%;
}

  .deleteAllDiv i {
    color: red;
  }

  .deleteAllDiv span {
    font-size: 20px;
